<template>
<nav class="bottom-nav main">
  <a href="#" class="bnav-item" :class="{ active: index === 0 }" 
    @click="changClass(0)">
    <div id="dd"><img src="../assets/images/xxgk.png" alt=""></div>
    <span>学校概况</span>
  </a>
  <a href="#" class="bnav-item " :class="{ active: index === 1 }"
      @click="changClass(1)">
    <div id="d2"><img src="../assets/images/lqqk.png" alt=""></div>
    <span>录取情况</span>
  </a>
  <a href="#" class="bnav-item " :class="{ active: index === 2 }"
      @click="changClass(2)">
    <div id="d3"><img src="../assets/images/zy.png" alt=""></div>
    <span>专业</span>
  </a>
  <a href="#" class="bnav-item " :class="{ active: index === 3 }"
      @click="changClass(3)">
    <div id="d4"><img src="../assets/images/tp.png" alt=""></div>
    <span>图片</span>
  </a>
  <a href="#" class="bnav-item " :class="{ active: index === 4 }"
      @click="changClass(4)">
    <div id="d5"><img src="../assets/images/lxgl.png" alt=""></div>
    <span>留学情况</span>
  </a>
</nav>

</template>

<script>
  export default{

    data(){
      return{
        index: 0
      }
    },
    components: {},
    methods: {
      changClass(id){
          this.index = id;
      }
    },
    ready(){
      // console.log(index);
    },
    created(){
      var index = 0;
    }
  }

</script>

<style scoped>
.bottom-nav{
  height: 50px;
  width: 100%;
  position: fixed;
  bottom: 0;
  z-index: 100;
  background-color: #fff;
}

.bnav-item{
 position: relative;
 display: table-cell;
 width: 1%;
 height: 50px;
 text-align: center;
 vertical-align: middle;
}

.bnav-item img{
  height: 20px;
}

.bnav-item span{
  font-size: .12rem;
}

table{
  width: 100%;
}

tr{
  line-height: 50px;
}

tr td{
  width: 20%;
  text-align: center;
}

.bn-lable{
  font-size: .13rem;
}

.active{
  color: blue;
}


#d1{
  background: url(../assets/images/lqqk.png) no-repeat center;
  background-size: 25px;
  height: 23px;
}
</style>



